                height: 100px !important;
            }
        }
        .two {
            .parent {
                h5 {
                    font-size: 20px;
                }
                h3 {
                    font-size: 25px;
                }
                p {
                    font-size: 18px;
                }
                img {
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
    .home-wrapper-5 {
        .blogCard {
            .cardImage {
                img {
                    width: 100%;
